.showcase-card {
  background: var(--primary);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10vh 0;
  min-height: 110vh;
}

.showcase-slider {
  width: 94%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 2px solid var(--black); */
}

.showcase-slider div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.showcase-inner-card {
  /* display: flex !important; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--black);
  margin: 1vw;
  border-radius: 5%;
  min-height: 100vh;
}

.showcase-row {
  width: 98%;
  padding: 2vh 2vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.showcase-info-container {
  width: 85%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.showcase-title {
  text-decoration: underline;
  font-size: 300%;
  color: var(--quatrinary);
  font-weight: bold;
  width: 90%;
  min-height: 10vh;
  text-align: center;
}

.showcase-img {
  width: 90%;
}

.showcase-description {
  font-size: 200%;
  color: var(--quatrinary);
  width: 90%;
  min-height: 10vh;
  text-align: center;
  padding: 1vh;
}

.showcase-buttons {
  height: 15% !important;
  width: 90%;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: space-between !important;
  padding: 2vh 0;
}

.showcase-buttons a {
  border: 2px solid var(--black);
  background-color: var(--quatrinary);
  padding: 1vh 10vh;
  color: var(--primary);
  transition: transform 0.2s;
  border-radius: 1vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 2px 2px 4px var(--black);
}

.showcase-buttons a:hover {
  color: var(--black);
  transform: scale(1.1);
  background-color: var(--secondary);
}

.tech-list-container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 100%;
  border: 1px solid var(--turq);
  min-width: 15vw;
  border-radius: 10px;
  padding: 10px;
  background-color: var(--secondary);
  box-shadow: 4px 4px 4px var(--black);
}

.showcase-list-title {
  color: var(--light);
}

.custom-arrow {
  width: 6vh;
  height: 50vh;
  background-color: var(--black);
  color: var(--white);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--turq);
  border-radius: 5px;
  padding: 1px;
  font-weight: bold;
  font-size: 3vh;
}

.tech-icon {
  height: 8vh;
  width: auto;
  color: var(--black);
  margin: 1vh;
}

.slick-dots {
  bottom: -2vh;
}

.button-icon-left {
  height: 4vh;
  width: 4vh;
  margin-right: 1vw;
}

.button-icon-right {
  height: 4vh;
  width: 4vh;
  margin-left: 1vw;
}

.highlight {
  transition: transform 0.2s;
}

.hbrs-highlight:hover {
  transform: scale(1.1);
  color: var(--handlebars);
}

.hbrs-highlight:hover .tech-icon {
  color: var(--handlebars);
}

.node-highlight:hover {
  transform: scale(1.1);
  color: var(--node);
}

.node-highlight:hover .tech-icon {
  color: var(--node);
}

.jq-highlight:hover {
  transform: scale(1.1);
  color: var(--jquery);
}

.jq-highlight:hover .tech-icon {
  color: var(--jquery);
}

.ex-highlight:hover {
  transform: scale(1.1);
  color: var(--express);
}

.ex-highlight:hover .tech-icon {
  color: var(--express);
}

.html-highlight:hover {
  transform: scale(1.1);
  color: var(--html);
}

.html-highlight:hover .tech-icon {
  color: var(--html);
}

.css-highlight:hover {
  transform: scale(1.1);
  color: var(--css);
}

.css-highlight:hover .tech-icon {
  color: var(--css);
}

.js-highlight:hover {
  transform: scale(1.1);
  color: var(--js);
}

.js-highlight:hover .tech-icon {
  color: var(--js);
}

.react-highlight:hover {
  transform: scale(1.1);
  color: var(--react);
}

.react-highlight:hover .tech-icon {
  color: var(--react);
}

.mongo-highlight:hover {
  transform: scale(1.1);
  color: var(--mongo);
}

.mongo-highlight:hover .tech-icon {
  color: var(--mongo);
}

.graph-highlight:hover {
  transform: scale(1.1);
  color: var(--graphql);
}

.graph-highlight:hover .tech-icon {
  color: var(--graphql);
}

.apollo-highlight:hover {
  transform: scale(1.1);
  color: var(--apollo);
}

.apollo-highlight:hover .tech-icon {
  color: var(--apollo);
