  color: var(--sequelize);
}

.sequelize-highlight:hover .tech-icon {
  color: var(--sequelize);
}

/* | @MEDIA QUERIES | */
@media screen and (max-width: 1100px) {
  .showcase-card {
    padding-top: 12vh;
  }

  .showcase-slider {
    max-height: 125vh;
  }

  .tech-list-container {
    width: 100%;
    justify-content: space-between !important;
  }

  .showcase-upper-row {
    flex-direction: column;
  }

  .showcase-list-title {
    display: none;
  }

  .showcase-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row !important;
    justify-content: space-between !important;
    list-style-type: none;
    padding-left: 0 !important;
    width: 100%;
  }

  .tech-icon {
    height: 6vh;
  }

  .showcase-lower-row a {
    padding: 1vh 5vh;
  }

  .showcase-row {
    flex-direction: column;
  }

  .tech-text {
    display: none;
  }
}

@media screen and (max-width: 1000px) {
  .showcase-buttons {
    flex-direction: column;
  }

  .showcase-buttons a {
    width: 45vw;
    margin: 1vh;
  }
}

@media screen and (max-width: 800px) {
  .showcase-slider {
    max-height: 150vh;
  }

  .showcase-title {
    font-size: 200%;
    margin-bottom: 1vh;
  }

  .showcase-description {
    font-size: 100%;
  }

  .showcase-upper-row {
    padding: 1vh;
  }

  .showcase-pic {
    width: 100%;
  }
  .showcase-pic img {
    height: auto;
    width: 75vw;
  }

  .showcase-lower-row {
    flex-direction: column;
  }

  .showcase-lower-row a {
    margin-top: 2vh;
    width: 100%;
  }

  .showcase-list-item {
    padding: 0 1vh;
  }

  .showcase-description {
    font-size: 150%;
    display: flex;
    text-align: center;
  }
}